.App {
  width: 100%;
  text-align: center;
  overflow-x: hidden;
}
.logo {
  width: 100px;
  margin: 20px 0px;
  cursor: pointer;
}
.main {
  width: 100vw;
  height: calc(100vh - 56px - 102.73px);
  position: relative;
}
.main video {
  width: 100vw;
  height: 100%;
  object-fit: cover;
}
.main video source {
  display: block;
  width: 100vw;
  height: 100%;
  object-fit: cover;
}
.video_text {
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translateY(-50%);
  color: #fff;
}
.video_text p:nth-of-type(1) {
  font-size: 1.2rem;
  padding-top: 2rem;
}
.video_text p:nth-of-type(2) {
  width: 8rem;
  background-color: #fff;
  padding: 0.5rem;
  color: #111;
}
.video_text p:nth-of-type(2) a {
  width: 100%;
  height: 100%;
  display: block;
}
.row {
  width: 100%;
  margin: 0px auto 50px;
}
.top {
  font-size: 25px;
  font-weight: bold;
  margin: 5rem 0px;
}
.col-md-4 {
  cursor: pointer;
}
.col-md-4 h4 {
  font-weight: bold;
  font-size: 16px !important;
  margin: 2rem 0px 1rem;
  height: 30px;
}
.col-md-4 p {
  font-size: 13px;
  padding: 0px 5px;
  height: 30px;
}
.col-md-4 p:nth-of-type(2) {
  font-size: 15px;
}
.content {
  font-size: 13px;
  height: 45px;
  padding: 5px;
}
.col-md-3 {
  cursor: pointer;
}
.col-md-3 h4 {
  font-weight: bold;
  font-size: 16px !important;
  margin: 2rem 0px 1rem;
  height: 30px;
}
.col-md-3 p {
  font-size: 13px;
  padding: 0px 5px;
  height: 30px;
}
.col-md-3 p:nth-of-type(2) {
  font-size: 15px;
}
.price {
  color: brown;
  font-weight: bold;
  font-size: 16px;
}
.price span {
  text-decoration: line-through;
  margin-right: 10px;
  font-size: 9px;
  color: black;
  font-weight: normal;
}
img {
  width: 100%;
}
.more {
  width: 80%;
  margin: 100px auto;
}
.more .row {
  gap: 50px;
}
.more .col {
  text-align: left;
}
.more .col:nth-of-type(2) {
  width: (100% / 1.2);
}
.more .col h4 {
  padding: 30px 0px;
  border-bottom: 1px solid #ddd;
}
.more .col p:nth-of-type(1) {
  font-size: 15px;
  padding-top: 30px;
  color: #aaa;
}
.more .col p:nth-of-type(2) {
  font-size: 13px;
  border-bottom: 1px solid #ddd;
  padding-bottom: 30px;
}
.more .col button:nth-of-type(3) {
  width: 280px;
}
.tabmenu {
  width: 90%;
  margin: 50px auto;
}
.t_c {
  padding: 10rem;
}
.footer {
  width: 100%;
  margin: 0px auto;
  font-size: 13px;
}
.fo_top {
  width: 90%;
}
.f_top ul {
  width: 100%;
  margin: 0px auto;
  display: flex;
  justify-content: space-around;
  list-style: none;
  background-color: #aaa;
  padding: 0.5rem;
}
.f_top ul a {
  color: #fff;
  text-decoration: none;
}
.f_bottom {
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
